a fotográfica. Comprendiendo esto, el obturador cuenta con dos cortinillas, que suben y bajan para realizar el movimiento, la clave de este efecto fotográfico es reducir el tiempo que tarda la segunda cortina en subir y bajar. Esto le permite capturar primero la trayectoria de un objeto o persona en movimiento (estilo de exposición larga) y finalmente, la segunda cortina con un flash, que le permite modificar la imagen mientras conserva el rastro detrás de ella. ")])])])]),_vm._m(2)],1)],1)}
